.frame-144 {
    align-items: center;
    background-color: var(--white);
    box-shadow: 0px 1px 0px #e5e9f2;
    display: flex;
    height: 70px;
    min-width: 768px;
    mix-blend-mode: normal;
    padding: 0 80px;
    position: relative;
}

@media (max-width: 375px) {
    .frame-144 {
        min-width: 375px;
        padding: 0 20px;
    }
}